.ImageList {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 0;
  grid-auto-flow: column;
  grid-auto-columns: calc(90% - 40px);
  overflow-x: auto;
  overflow-y: hidden;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.ImageList::-webkit-scrollbar {
  display: none;
}

.ImageList::before,
.ImageList::after {
  content: '';
  width: 10px;
}

/* Sharing */
.ImageList-share {
  width: 100%;
  text-align: center;
}

.react-sharing-button__link {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  margin: 0.5em;
  border-radius: 5px;
  padding: 0.5em 0.75em;
  font: 400 0.8em Arial;
}

.react-sharing-button__icon {
  fill: #fff;
  stroke-width: 0;
  width: 1.2em;
  height: 1.2em;
  line-height: 1.2em;
  vertical-align: bottom;
}

.react-sharing-button__text {
  padding-left: 0.4em;
  line-height: 1.2em;
}

.react-sharing-button--download {
  background-color: #A32C19;
  cursor: pointer;
  border: none;
}

.react-sharing-button--external {
  background-color: #25D366;
  cursor: pointer;
  border: none;
}

.resp-sharing-button__link,
.resp-sharing-button__icon {
  display: inline-block
}

.resp-sharing-button__link {
  text-decoration: none;
  margin: 0.5em
}

.resp-sharing-button {
  border-radius: 5px;
  padding: 0.5em 0.75em;
}

.resp-sharing-button__icon svg,
.resp-sharing-button__icon img {
  width: 1em;
  height: 1em;
  margin: 0;
  vertical-align: middle
}

.resp-sharing-button__icon--solid {
  fill: #fff;
  stroke: none
}

.resp-sharing-button--instagram {
  background-image: -webkit-gradient( linear, left bottom, left top, from(#E09B3D), color-stop(30%, #C74C4D), color-stop(60%, #C21975) );
  background-image: -webkit-linear-gradient( bottom, #E09B3D, #C74C4D 30%, #C21975 60% #7024C4 );
  background-image: linear-gradient( 0deg, #E09B3D, #C74C4D 30%, #C21975 60% #7024C4 )
}

.resp-sharing-button--facebook {
  background-color: #3b5998;
}

.resp-sharing-button--twitter {
  background-color: #55acee;
}

