/* Duotone */
.duotone {
  position: relative;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.duotone::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  bottom: 0px;
  right: 0;
  pointer-events: none;
}

.yellow-red {
  background-color: rgba(221, 204, 0, 1);
}

.yellow-red img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: darken;
}

.yellow-red::after {
  background-color: rgba(221, 0, 0, 1);
  mix-blend-mode: lighten;
  color: white;
}

.green-blue {
  background-color: #00ff36
}

.green-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: multiply;
}

.green-blue::after {
  background-color: #23278a;
  mix-blend-mode: lighten;
  color: white;
}

.purple-green {
  background-color: #88169d;
}

.purple-green img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: lighten;
}

.purple-green::after {
  mix-blend-mode: hard-light;
  background: -webkit-gradient(linear, right bottom, left top, from(#75d775), to(#321a5b));
  background: -webkit-linear-gradient(bottom right, #75d775, #321a5b);
  background: linear-gradient(to top left, #75d775, #321a5b);
}

.pink-blue {
  background-color: #3A34FF;
}

.pink-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: hard-light;
}

.pink-blue::after {
  mix-blend-mode: darken;
  background: #FF49BA;
}

.red-blue {
  background-color: #F7392B;
}

.red-blue img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: multiply;
}

.red-blue::after {
  mix-blend-mode: lighten;
  background: #16006F;
}

.orange-green {
  background-color: #70F134;
}

.orange-green img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: luminosity;
}

.orange-green::after {
  mix-blend-mode: multiply;
  background: #FF8B02;
}

.orange-black {
  background-color: #010101;
}

.orange-black img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: luminosity;
}

.orange-black::after {
  mix-blend-mode: overlay;
  background: #F98900;
}

.violet-marine {
  background-color: #002333;
}

.violet-marine img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: hard-light;
}

.violet-marine::after {
  mix-blend-mode: hard-light;
  background: #8E5EB0;
}

.green-brown {
  background-color: #832241;
}

.green-brown img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: hard-light;
}

.green-brown::after {
  mix-blend-mode: darken;
  background: #84D043;
}

.yellow-turquoise {
  background-color: #05606C;
}

.yellow-turquoise img {
  -webkit-filter: grayscale(100%) contrast(1.2);
          filter: grayscale(100%) contrast(1.2);
  mix-blend-mode: hard-light;
}

.yellow-turquoise::after {
  mix-blend-mode: darken;
  background: #EBDF28;
}

